let constUrl;
export default{
	data(){
		return{
				openId:"",
				canIUse: wx.canIUse('button.open-type.getUserInfo')
		}
	
	},
	onLoad(){
		let that=this;
		constUrl=that.$consturl;
		// console.log(constUrl);
		// console.log("授权");
		uni.showModal({
			title: '提示',
			content:'请授权',
		    success: function(res) {
				// console.log("授权成功");
				wx.login({
				  provider: 'weixin',
				  success: function (loginRes) {
					  // console.log("wx.login成功");
				let code=loginRes.code;
				  wx.getSetting({
				        success (res){
							 // console.log("getSetting成功");
							 // console.log(res.authSetting);
				          if (res.authSetting) {
				            // 已经授权，可以直接调用 getUserInfo 获取头像昵称
				            wx.getUserInfo({
				            success: function(infoRes) {
								   // console.log("getUserInfo成功");
				                // console.log(res.userInfo);
								console.log(infoRes.userInfo);
								let userInfo=infoRes.userInfo,ts=that;
								let urL=constUrl,nickName=userInfo.nickName,city=userInfo.city,avataUrl=userInfo.avatarUrl;
							uni.request({
								url: urL + "wx/user/login", //
								data: {
									code:code,
									nickName:nickName,
									city:city,
									avataUrl:avataUrl
								},
								header: {
										'Content-Type':'application/x-www-form-urlencoded;charset=utf-8'
								      },
								method: 'POST',
								success: function (res) {
								console.log(res.data);
								let id=res.data.openid;
								// console.log(id);
								uni.setStorageSync("openid",id);
								// uni.setStorageSync("openid","test");
								uni.setStorageSync("identity",1);
																		// console.log(id);
								console.log(uni.getStorageSync("openid"));
								},
									complete:function(){
								// console.log("complete");
										}
								});
				              },
							  fail:function(){
								  // console.log(若授权失败,请点击授权登录按钮重新登录);
							  }
				            })//info
				          }
				        }
				      });//get
				    }
				});
			},
		
		});
	},
	methods:{
		company:function(e){
			// console.log(e.detail.value);
			let phone=e.detail.value.phoneValue,name=e.detail.value.nameValue, department=e.detail.value.departmentValue;
			let openid=uni.getStorageSync("openid");
			uni.request({
				url: constUrl + "wx/user/checkPhone", //
				data: {
					phone:phone,
					name:name,
					openid:openid
				},
				header: {
						'Content-Type':'application/x-www-form-urlencoded;charset=utf-8'
				      },
				method: 'POST',
				success: function (res) {
				let msg=res.data.msg
				
				if(msg=="failed"){
					uni.showModal({
						title: '提示',
						content: '登录失败，是否重新登录',
						success: function(res) {
							if (res.confirm) {
								// uni.redirectTo({
								// 	url: '../index/index'
								// });
							} else if (res.cancel) {
								// console.log('用户点击取消');
								uni.clearStorageSync();
								uni.reLaunch({
									url: '../../home/home'
								});
							}
						}
					});
				}else{
					uni.reLaunch({
					    url: "../pHome"
					});
				}
				},
				fail:function(){
				// console.log("fail");
				
						}
				});
			
		}
	}
}
